.custom-modal .modal-content {
    border-radius: 20px;
  }
  .custom-modal  {
    top: -65px;
  }

  .custom-modal-header {
    display: flex;
    justify-content: center;
    position: relative;
  }
  
  .custom-modal-header .modal-title {
    flex-grow: 1;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 60%;
    transform: translateX(-50%);
    font-weight:bold;
    font-size: 28px;
    color: black;
  }

  body.modal-open {
    overflow: hidden;
  }
  
  body.modal-open .App {
    filter: blur(5px);
  }
  
  .col-50 {
    width: 50%;
    float: left;
    padding: 100px 40px;
    box-sizing: border-box;
  }
  
  /* Sign Up Form
  ================================================== */
  .sign-up-form h1 {
    text-align: center;
    text-transform: uppercase;
  }
  
  /* Form Group
  ================================================== */
  .form-group {
    max-width: 100%;
    border-radius: 4px;
    position: relative;
    margin-top: 32px;
  }
  .form-group input {
    width: 100%;
    box-sizing: border-box;
    height: 52px;
    padding: 0 12px;
    font-size: 17px;
    letter-spacing: 0.25px;
    border-radius: 4px;
    border: 2px solid #cecece;
    color: #cecece;
  }
  .form-group label {
    pointer-events: none;
    position: absolute;
    margin: 0;
    top: 2px;
    left: 20px;
    line-height: 24px;
    font-size: 16px;
    color: #cecece;
    padding: 12px 0;
    transition: all 0.3s ease;
    text-transform: capitalize;
  }
  .form-group input:active,
  .form-group input:focus {
    outline: none;
    border: 2px solid #efbad3;
  }
  .form-group input::selection {
    background-color: #cecece;
  }
  .form-group input:focus + label,
  .form-group input:not(:placeholder-shown) + label {
    transform: translate(-20px, -42px);
    font-size: 14px;
    color: #efbad3;
  }
  
  /* Separate Line
  ================================================== */
  hr {
    border-top: 2px solid #cecece !important;
    width: 100%;
    margin: 40px 0;
  }
  .events {
    display: -webkit-inline-box;
    font-size: 16px;
    text-transform: uppercase;
    position: relative;
    top: -57px;
    background-color: black;
    padding: 0px 12px;
    color: white;
  }
  .or {
    display: -webkit-inline-box;
    font-size: 16px;
    text-transform: uppercase;
    position: relative;
    top: -57px;
    left: 45%;
    background-color: #fff;
    padding: 0px 12px;
    color: #4f4e4e;
  }
  @import url(https://fonts.googleapis.com/css?family=Open+Sans);

  /* Google Button
  ================================================== */
  .google-btn {
    border: 1px solid black;
    border-radius: 20px;
    height: 41px;
    margin-top: 35px;
    text-align: center;
    transition: all 0.3s ease;
  }
  .google-btn:hover {
    border: 2px solid #46CDD0;
    cursor: pointer;
    box-shadow: 0 0 20px 0px #46CDD0;
  }
  .google-btn img {
    margin-right: 10px ;
  }
  .google-btn p {
    font-size: 15px;
    color: black;
    font-weight: bold;
    text-transform: capitalize;
    display: -webkit-inline-box;
  }

